@keyframes cell-appear {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes cell-pop {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes score-diff-move {
  0% {
    top: 0;
    opacity: 1;
  }
  100% {
    top: -200px;
    opacity: 0;
  }
}

@keyframes message-appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
