@use '../../scss/variables';

.Header {
  display: flex;
  gap: variables.$gap;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 24px;
  padding: variables.$gap;

  &-Heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-Name {
    font-size: 4rem;
  }

  &-Controls {
    display: flex;
    justify-content: flex-end;
  }

  &-Info {
    display: flex;
    gap: 16px;
    align-items: center;
    justify-content: center;
  }
}
